


























import service from '../../common/service.js'
export default {
	data() {
		return {
			active:1,
			money:'99',
			vipLevel:""
		};
	},
	
	methods:{
		bevip1(){
			if(this.vipLevel == 'VIP1'){
				uni.showToast({
					icon:'none',
					title: '您已经是初级勇士，请点击中级',
				});
				return;
			}
			if(this.vipLevel == 'VIP2'){
				uni.showToast({
					icon:'none',
					title: '您已经是中级勇士，无须再次购买',
				});
				return;
			}
			uni.showModal({
				title:'微品提示',
				content:'确定支付99成为初级勇士',
				success:function(res){
					if (res.confirm) {
						uni.navigateTo({
							url:'../MoneyWay/request-payment?money=99',
						})
					} else if (res.cancel) {
					}
					
				}
			})
		},
		bevip2(){
			if(this.vipLevel == 'VIP2'){
				uni.showToast({
					icon:'none',
					title: '您已经是中级勇士，无须再次购买',
				});
				return;
			}
			uni.showModal({
				title:'微品提示',
				content:'确定支付999成为中级勇士',
				success:function(res){
					if (res.confirm) {
						uni.navigateTo({
							url:'../MoneyWay/request-payment?money=999',
						})
					} else if (res.cancel) {
					}
				}
			})
		},
	},
	onLoad:function(){
		this.vipLevel = uni.getStorageSync('vipLevel') ?uni.getStorageSync('vipLevel') :"";
	}
}



